/**
 * Helper functions for customer pages */


export function truncateText(text, index){
  if(text){
    return text.length > index ? text.substring(0, (index - 3)) + '...' : text
  }
}
// use INTL.numberformat to do the currency. 
export function formatCurrency(value, currency = 'USD'){
  const formatter = new Intl.NumberFormat('en-US',{
    style: 'currency', currency: currency
  });
  if(!isNaN(parseFloat(value))){
    return formatter.format(value);
  }
  return value; 
  
}
/**
 * render list taxes for Payment and Invoice page */ 
export function renderTax(taxes, total){
  if(taxes.length){
    return ( 
      taxes.map((item, ind) => {
        return(
          <div className='d-flex mb-1 justify-content-between align-items-center' key={ind}>
            <div>{item.label}</div>
            <div className='fw-bold'>
              {formatCurrency(item.calculate(total))}
            </div>
          </div>
        )
      })
    )
  }
}

/**
 * add 'from' before price to indicate the price may change 
 * depending on variant and modifier selection 
**/ 
export function renderFromText(item) {
  if(Object.keys(item).length){
    if(item.variants?.length || item.modifiers?.length){
      return 'from';
    }
  }
  return null;
}