/**
 * This is the category pills UI on the menu page 
 * Some discussion here. We are using a scrollspy method to auto select the category
 * when scrolling through the menu. I don't think we need to setCategory for now. 
 **/
import { Nav } from "react-bootstrap";
import React, { useState,useEffect } from "react";
import styles from '../assets/styles/MenuCategory.module.css';
import ScrollSpyNav from 'react-scrollspy-nav';
export default function MenuCategory(props){
  const {categories} = props;
  // force scrollSPy to re-render component set a temporary timeout 
  const [render, setRender] = useState(false);
  useEffect(() => {
    setRender(false);
    setTimeout(() => {setRender(true)}, 5);
  }, [categories])
  return (
    // map categories array here 
    categories.length && render ? 
    (
      <ScrollSpyNav
        scrollTargetIds={(categories).map(i => `category-header-${i.id}`)}
        activeNavClass={styles.menucategory_link__active}
        scrollDuration="150"
      >
        <div className="overflow-auto p-3">
          <Nav
            className="d-flex flex-nowrap"
            id="menuCategory-tab"
            >
            {categories.map((item) => {
              return (
                <Nav.Item 
                  key={item.id}
                  className="flex-shrink-0">
                  <Nav.Link 
                    href={`#category-header-${item.id}`}
                    className={styles.menucategory_link}
                  >
                    {item.label}
                  </Nav.Link>
                </Nav.Item>
              )
            })}
          </Nav>
        </div>
      </ScrollSpyNav>
    ) :
    (
      <div style={{height:'64px'}}></div>
    )
  )
}