/**
 * The checkout summary page
 * url: /customer/checkout/:restaurant/:table
 * This is the cart/summary page of the ordering*/

import React, { useState, useEffect, useRef } from 'react';
import {
  Container,
  Row,
  Col,
  Form,
  Button,
  Card,
  Image,
  ListGroup,
} from 'react-bootstrap';
import { useParams, useHistory } from 'react-router-dom';
import NavbarCust from '../components/NavbarCust';

// Restaurant logo
import { formatCurrency } from '../components/Helper';

// Components
import CheckoutSummaryOrderList from '../components/CheckoutSummaryOrderList';
import CheckoutSummaryFooter from '../components/CheckoutSummaryFooter';
import { useCustContext } from '../CustomerContext';
import { deleteOrderItem, getOrderInfo } from '../api';
import { ItemDrawer } from '../components/ItemDrawer';
export default function CheckoutSummmary() {
  const { table_id } = useParams();
  const history = useHistory();
  const { order, setItemId } = useCustContext();
  const [total, setTotal] = useState();
  const [amount, setAmount] = useState();
  const [orderSummary, setOrderSummary] = useState([]);
  const [editItemId, setEditItemId] = useState();
  const getOrder = async () => {
    try {
      if (order.id) {
        const res = await getOrderInfo(order.id);
        setOrderSummary(res.data.data.order_items);
        setTotal(res.data.data.total);
        let num = 0;
        res.data.data.order_items.forEach((item) => {
          num = num + item.quantity;
        });
        setAmount(num);
      }
    } catch (err) {
      console.log(err);
    }
  };
  const handleDeleteOrderItem = async (id) => {
    try {
      const res = await deleteOrderItem(id);
      console.log(res.data);
      getOrder();
    } catch (err) {
      console.log(err);
    }
  };
  const handleEditOrderItem = (order_item_id, menu_item_id) => {
    // history.push(
    //   `/customer/table-ordering/${table_id}/menu/${menu_item_id}/${order_item_id}`
    // );

    // use ItemDrawer instead 
    setItemId(menu_item_id);
    setEditItemId(order_item_id);
  };
  const handleCloseItemDrawer = () => {
    getOrder();
  }


  useEffect(() => {
    if(window){window.scroll(0,0)}
  }, [])
  useEffect(() => {
    getOrder();
  }, [order]);
  return (
    <div id='scroll'>
      <NavbarCust goBack={true} title={'Order Summary'} />
      <ItemDrawer 
        handleCloseItemDrawer={handleCloseItemDrawer}
        editItemId={editItemId}
      />
      <Container fluid className='pt-3'>
        {/* <div className="d-flex justify-content-between align-items-center">
          <RestaurantLogo
            orientation="horizontal"
            logo={restaurantData.logo}
            name={restaurantData.name}
            />
          <Button
            variant="light"
            size="sm"
            className="pull-right"
            onClick={() => history.goBack()}
            >
              Order More
          </Button>
        </div> */}
        <div className='text-center'>
          <div className='text-muted small'>{amount} dish selected</div>
          <h4 className='fw-bold'>
            Total: {formatCurrency(total)}
          </h4>
        </div>
      </Container>
      {/* Display all the orders */}
      <CheckoutSummaryOrderList
        orders={orderSummary}
        handleDeleteOrderItem={handleDeleteOrderItem}
        handleEditOrderItem={handleEditOrderItem}
      />
      <CheckoutSummaryFooter getOrder={getOrder} orders={orderSummary} />
    </div>
  );
}
