import {React, useState, useEffect} from "react";
import { useHistory, useParams } from "react-router";
import { useCustContext } from "../CustomerContext";
import MenuBrand from "./MenuBrand";
import {Container, Row, Col} from 'react-bootstrap';
import cx from 'classnames';
import styles from '../assets/styles/MenuBrand.module.css';
import { AiOutlineLoading3Quarters } from "react-icons/ai";
export default function BrandList(props){
  const {brands, selectedBrand, handleSelectBrand, tableInfo, getBrands} = useCustContext();
  const {table_id} = useParams();
  const history = useHistory();
  const [loading, setLoading] = useState(false);
  const clickSelectBrand = (item) => {
    handleSelectBrand(item);
    if(props.goBack){
      history.goBack();
    }
    if(props.goMenu){
      history.push(`/customer/table-ordering/${table_id}/menu`)
    }
  }
  useEffect(() => {
    // check if brands empty. 
    if(! brands.length){
      setLoading(true);
      getBrands(table_id, false);
    }
    if(brands.length){
      setLoading(false);
    }
    if(selectedBrand.id && props.scrollable){
      const scrollableDiv = document.getElementById('brand-scrollable-container');
      const selectedBrandDiv = document.getElementById(`brandCol-${selectedBrand.id}`);
      // scroll div to current selected brand 
      let offsetLeft = selectedBrandDiv.offsetLeft;
      let width = selectedBrandDiv.scrollLeft;
      scrollableDiv.scroll(offsetLeft - (width + 60) , 0);
    }
  }, [brands])
  return (
    <>
    {
      loading && 
      <div className="text-center">
        <AiOutlineLoading3Quarters className="icon-spin text-primary" size={50}/>
      </div>
    }
    <Container fluid className={cx({
      'mt-3 px-0' : props.scrollable,
      'mt-4' : !props.scrollable
    })}>
      <div id={'brand-scrollable-container'}  className={cx({'ps-4 overflow-auto' : props.scrollable})}>
        <Row className={cx({'flex-nowrap' : props.scrollable})}>
        {brands.map((item, index) => {
          return (
          <Col 
            xs="6" 
            md="4" 
            key={index} 
            onClick={() => clickSelectBrand(item)}
            className={cx({
              [styles.brandCol] : props.scrollable
            })}
            id={`brandCol-${item.id}`}
          >
            <MenuBrand
              logo={item.logo?.public_url}
              background={item.cover_photo?.public_url}
              text={item.name}
              selected={item.id === selectedBrand.id ? true : false}
            />
          </Col>
          );
        })}
        </Row>
      </div>
    </Container>
    </>
  )

}