import React from 'react';
import NavbarCust from '../components/NavbarCust';
import { useCustContext } from '../CustomerContext';
import BrandList from '../components/BrandList';
import { Container, Image } from 'react-bootstrap';
function Brands(props) {
  const { tableInfo, brands } = useCustContext();
  return (
    <div className='position-relative'>
      <NavbarCust 
        page={'home'}
      />
      <div className="text-center"
        style={{
          backgroundImage: `url(${tableInfo.location?.background_photo?.public_url})`,
          backgroundSize: 'cover',
          backgroundRepeat: 'no-repeat',
        }}>
        <Image
          className="mt-4" 
          src={tableInfo.location?.logo?.public_url}/>
      </div>
      {
        brands && 
        <> 
          <Container fluid className="mt-3 text-center"> 
            <h4>Select a Brand</h4>
          </Container>
          <BrandList
            goMenu
          />
        </>
      }
    </div>
  );
}

export default Brands;
