/**
 * single menu item card*/

import { Card, Row, Image, Col } from "react-bootstrap";
import styles from "../assets/styles/MenuCardList.module.css";
import { formatCurrency, truncateText } from "./Helper";
import cx from "classnames";
import { useEffect, useState } from "react";
import defaultImage from '../assets/images/menu-img-placeholder.png';
import { useCustContext } from "../CustomerContext";
export default function MenuCard(props) {
  const {selected, addFromText} = props; 
  // we need to loop through the different variation of the item. 
  const [quantity, setQuantity] = useState(0);
  const {setScrollY, setItemId} = useCustContext();
  const onClickHandler = (e) => {
    if (props.id && props.available) {
      // save current scoll pos for menu
      // const element = document.getElementById(`${props.id}-sc`);
      // setScrollY(element.offsetTop);
      // props.clickViewItem(props.id);
      setItemId(props.id);
    }
  };
  useEffect(() => {
    if(selected.length){
      let num = 0;
      selected.forEach((item)=> {
        num += item.quantity;
      })
      setQuantity(num);
    }
  },[selected])
  return props.name ? (
    <Card
      className={cx(styles.card_menu, {
        [styles.bg_unavailable]: !props.available,
        [styles.card_menu__selected]: props.selected.length,
      })}
      onClick={onClickHandler}
      id={`${props.id}-sc`}
    >
      <Row className="g-0">
        <Col xs="3" md="4">
          {/* set a default image if unavailable */}
          <Image src={(props.image) ? props.image : defaultImage} fluid className={styles.card_menu__image} />
        </Col>
        <Col xs="9" md="8">
          <Card.Body
            className={cx("text-left", {
              [styles.text_unavailable]: !props.available,
            })}
          >
            <h2
              className={cx(styles.card_menu__title, {
                [styles.text_unavailable]: !props.available,
                [styles.card_menu__title__selected]: props.selected.length,
              })}
            >
              {props.name}
            </h2>
            <p className={styles.card_menu__description}>
              {truncateText(props.description, props.truncateCount)}
            </p>
            <div className="d-flex mt-1 align-items-center justify-content-between">
              <div className="d-flex">
                {props.price_discount === 14 && (
                  <p style={{ color: "#808D9A", marginBottom: 0, textDecoration: 'line-through', marginRight: 5}}>${200.0}</p>
                )}
                <div>
                  <span className="small">{addFromText}</span>
                  <div className={`ms-1 h3 d-inline-block ${styles.card_menu__price}`}>
                    {formatCurrency(props.price)}
                  </div>
                </div>
              </div>
              {quantity > 0 && (
                <div>
                  x
                  <span className="fw-semibold">{quantity}</span>
                </div>
              )}
            </div>
          </Card.Body>
        </Col>
      </Row>
    </Card>
  ) : null;
}
