/**
 * Order status allows the customer to view the current 
 * progress of their orders*/ 

import {React, useState, useEffect} from "react";
import { useCustContext } from "../CustomerContext";
import styles from '../assets/styles/OrderStatus.module.css';
import cx from 'classnames';
export default function OrderStatus(props){
  const {orderFulfillment} = props;
  const [status, setStatus] = useState(1);

  const steps = [
    {
      label: 'Order Received',
      id: 1,
    },
    {
      label: 'Preparing',
      id: 2,
    },
    {
      label: 'Completed',
      id: 3
    }
  ]

  useEffect(() => {
    if(Object.keys(orderFulfillment).length){
      // update the status 
      setStatus(orderFulfillment.status);
    }
  }, [orderFulfillment])
  return (
    <div className={styles.stepper_wrapper}>
      {steps.map(item => {
        return (
          <div className={cx(styles.stepper_item, {
            [styles.stepper_item__completed]: status > item.id,
            [styles.stepper_item__active] : status == item.id
          })}>
            <div className={cx(styles.step_counter, {
              [styles.step_counter__completed] : status >= item.id
            })}></div>
            <div>{item.label}</div>
          </div>
        )
      })}
    </div>
  )
}