/**
 * This is the sticky footer cart that is appended below the page of the customer*/

import React, { useState, useEffect } from "react";
import { Container, Card, Row, Col, Button } from "react-bootstrap";
import { useHistory, useParams } from "react-router-dom";
import CustomerFooter from "../components/CustomerFooter";
import { formatCurrency } from "./Helper";
export default function FooterCart(props) {
  // get the table no and restaurant
  const { table_id } = useParams();
  const history = useHistory();
  const [total, setTotal] = useState(0);
  const [amount, setAmount] = useState(0);
  const { orderItems } = props;
  useEffect(() => {
    if (orderItems.total) {
      setTotal(orderItems.total);
    }
    if (orderItems.order_items) {
      let num = 0;
      orderItems.order_items.forEach((item) => {
        num = num + item.quantity;
      });
      setAmount(num);
    }
  }, [orderItems]);
  return (
    <CustomerFooter classPrefix="py-0">
      <Row className="justify-content-center">
        <Col xs="12" md="6">
          <Card className="border-0">
            <Row className="g-0 justify-content-between">
              <Col xs="6" md="">
                <Card.Body className="py-2">
                  <span 
                    className="text-muted"
                    style={{fontSize: '0.75em'}}
                  >
                    {amount} Items selected
                  </span>
                  <h4 className="fw-bold mt-0 mb-0">
                    {formatCurrency(total)}
                  </h4>
                </Card.Body>
              </Col>
              <Col xs="6" className="text-end g-0">
                {/* if order is empty, we shouldn't allow them to checkout  */}
                <Button
                  disabled={!total}
                  style={(!total) ? {
                    opacity: 0.5
                  } : null}
                  onClick={() => {
                    history.push(`/customer/checkout/${table_id}`);
                  }}
                  className="h-100 w-100 rounded-0 font-tp rounded-end"
                >
                  Check out
                </Button>
              </Col>
            </Row>
          </Card>
        </Col>
      </Row>
    </CustomerFooter>
  );
}
