/**
 * Clicking on this item on the menu will open up a drawer instead 
 **/ 

import React, {useState,useEffect} from 'react';
import { Container, Row, Card, Offcanvas, Button, Form } from 'react-bootstrap';
import { useCustContext } from '../CustomerContext';
import { formatCurrency, renderFromText } from './Helper';
import ItemDetailModifier from '../components/ItemDetailModifier';
import ItemDetailVariant from './ItemDetailVariant';
import ItemDetailFooter from '../components/ItemDetailFooter';
import { ReactComponent as NoteSvg } from '../assets/icons/notes.svg';

import {
  addToCart,
  deleteOrderItem,
  getItemDetails,
  getOrderInfo,
  updateOrderItem,
} from '../api';
export function ItemDrawer(props){
  const {handleCloseItemDrawer, editItemId} = props;
  const {itemId, setItemId, order} = useCustContext();
  const [show, setShow] = useState(false);
  const [itemInfo, setItemInfo] = useState({});
  const [selectedModifiers, setSelectedModifiers] = useState([]);
  const [extraNotes, setExtraNotes] = useState('');
  const [price, setPrice] = useState(0);
  const [itemQuantity, setItemQuantity] = useState(1);
  const [orderItem, setOrderItem] = useState({});
  const [groupVariant, setGroupVariant] = useState();
  const handleClose = () => {
    // reset information delay based on transition 
    setTimeout(() => {
      setItemId(null);
      setItemInfo({});
      setGroupVariant([]);
      setSelectedModifiers([]);
      setExtraNotes('');
      setItemQuantity(1);
      setPrice(0);
    }, 500);
    setShow(false);
    handleCloseItemDrawer();
  }

  const handleSelectModifiers = (item) => {
    let mod = [];
    item.forEach((item1) => {
      item1.forEach((item2) => {
        mod.push(item2);
      });
    });
    console.log(mod);
    setSelectedModifiers(mod);
  };
  const handleSetGroupVariant = (arr) => {
    let joinArr = arr.join(' - ');
    if (itemInfo.group_variants.length) {
      let group_var = itemInfo.group_variants[0].options.find(
        (item) => item.name === joinArr
      );
      console.log(group_var);
      setGroupVariant(group_var);
    }
  };
  const getItemDetail = async () => {
    const res = await getItemDetails(itemId);
    if(res.status === 200){
      await setItemInfo(res.data?.data);
    }
  }
  const getOrder = async () => {
    try {
      if (order.id) {
        const res = await getOrderInfo(order.id);
        let order_item = res.data.data.order_items.find((item) => {
          return item.id == editItemId;
        });
        console.log(order_item);
        setOrderItem(order_item);
        setSelectedModifiers(order_item.modifiers);
        setGroupVariant(order_item.variants);
        setItemQuantity(order_item.quantity);
        if(order_item.extra_notes){
          setExtraNotes(order_item.extra_notes);
        }
      }
    } catch (err) {
      console.log(err);
    }
  };
  const handleUpdatetoCart = async () => {
    let formData = {
      quantity: itemQuantity,
    };

    let modifiers = selectedModifiers.map((item) => {
      return { ...item, quantity: itemQuantity };
    });
    formData['modifiers'] = modifiers;
    if(extraNotes){
      formData.extra_notes = extraNotes;
    }
    if (groupVariant?.name) {
      formData['variant'] = {
        name: groupVariant.name,
        price: parseFloat(groupVariant.price),
        quantity: itemQuantity,
      };
    } else {
      formData['variant'] = {};
    }
    console.log(formData);
    try {
      const res = await updateOrderItem(editItemId, formData);
      console.log(res.data);
      handleClose();
    } catch (err) {
      console.log(err);
    }
  };
  const handleAddToCart = async () => {
    let formData = {
      order_items: [
        {
          menu_item_id: parseInt(itemId),
          quantity: itemQuantity,
        },
      ],
    };
    if(extraNotes){
      formData.order_items[0].extra_notes = extraNotes;
    }

    let modifiers = selectedModifiers.map((item) => {
      return { ...item, quantity: itemQuantity };
    });
    formData.order_items[0]['modifiers'] = modifiers;

    if (groupVariant?.name) {
      formData.order_items[0]['variants'] = {
        name: groupVariant.name,
        price: parseFloat(groupVariant.price),
        quantity: itemQuantity,
      };
    } else {
      formData.order_items[0]['variants'] = {};
    }
    try {
      const res = await addToCart(order.id, formData);
      // a flag to send Menu.js to re-fetch orders api
      if(res?.data){
        handleClose();
      }
    } catch (err) {
      console.log(err);
    }
  };
  useEffect(() => {
    if(itemId){
      setShow(true);
      getItemDetail();
      if(editItemId){
        console.log(editItemId);
        getOrder();
      }
    }
  }, [itemId])
  useEffect(() => {
        // update price
        if (itemQuantity > 0) {
          setPrice(itemInfo?.price)
          // we need to take into consideration the additional top up
          let modifierPrice = 0;
          selectedModifiers.forEach((item) => {
            modifierPrice = modifierPrice + parseFloat(item.price);
          });
          if (groupVariant?.price) {
            setPrice(
              itemQuantity * (parseFloat(groupVariant?.price) + modifierPrice)
            );
          } else {
            setPrice(itemQuantity * (parseFloat(itemInfo?.price) + modifierPrice));
          }
        }
  }, [itemId, itemQuantity, itemInfo, selectedModifiers, groupVariant, orderItem])
  return (
    <Offcanvas show={show} onHide={handleClose} placement={'end'} className="overflow-auto" style={{
      width:'100%'
    }}>
      <Offcanvas.Header
        className="d-block"
        style={{minHeight: '250px',}}
      >
        <Button
          variant='dark'
          className='rounded-circle'
          onClick={handleClose}
        >
          <i className='bi bi-arrow-left-short fs-4 text-white'></i>
        </Button>
      </Offcanvas.Header>
      <img src={itemInfo.image?.public_url} className="position-absolute" style={{height:'250px', width:'100%', objectFit:'cover', zIndex:'-1'}}/>
      <Container fluid className="mt-4">
        <div className='d-flex justify-content-between align-items-center'>
          <h3 className='text-secondary fw-semibold'>{itemInfo?.name}</h3>
          <div>
            <div className='d-inline-block small text-gray'>
              {renderFromText(itemInfo)}
            </div>
            <div className='d-inline-block ms-1 h4 fw-normal fw-tp'>
              {formatCurrency(itemInfo?.price || 0)}
            </div>
          </div>
        </div>
      </Container>
      <ItemDetailModifier
          modifiers={itemInfo?.modifiers}
          orderModifiers={orderItem?.modifiers}
          editItemId={editItemId}
          required={true}
          handleSelectModifiers={handleSelectModifiers}
          type='checkbox'
      />
      {/* Item Detail Varients */}
      <ItemDetailVariant
        variants={itemInfo?.variants}
        editItemId={editItemId}
        orderVariants={orderItem?.variants}
        type='radio'
        handleSetGroupVariant={handleSetGroupVariant}
      />
      <Container fluid className="mt-1"
        style={{marginBottom:'85px'}}
      >
        <Form>
          {/* special instructions section */}
          <Form.Group className='d-flex align-items-center'>
            <Form.Label className='mb-0'>
              <span className='text-primary'>
                <NoteSvg />
              </span>
            </Form.Label>
            <Form.Control
              placeholder='Extra note: E.g no pickles, more sauce..'
              className='border-0 input-sm'
              value={extraNotes}
              onChange={(e) => setExtraNotes(e.target.value)}
            ></Form.Control>
          </Form.Group>
        </Form>
      </Container>
      {/* Add to cart footer */}
      <ItemDetailFooter
        quantity={itemQuantity}
        setQuantity={setItemQuantity}
        price={price}
        handleAddToCart={handleAddToCart}
        handleUpdatetoCart={handleUpdatetoCart}
        editItemId={editItemId}
      />
    </Offcanvas>
  )
}