import 'bootstrap-icons/font/bootstrap-icons.css';
// import 'antd/dist/antd.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';
import './App.scss';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';

import { useState } from 'react';

// VIEWS

// Customer Views
import HomeCust from './pages/Home';
// Customer Sign in
import SignInCust from './pages/SignIn';
// Customer Sign up
import SignUpCust from './pages/SignUp';
// Customer Menu
import MenuCust from './pages/Menu';
// Customer Item Detail
import ItemDetailCust from './pages/ItemDetail';
// Customer Checkout Summary
import CheckoutSummmaryCust from './pages/CheckoutSummary';
// Customer Pay Check
import PayCheckCust from './pages/PayCheck';
// Customer Pay Check Invoice
import PayCheckInvoiceCust from './pages/PayCheckInvoice';
import NotFoundCust from './pages/404';
import BrandsCust from './pages/Brands';
import ConfirmOrderCust from './pages/ConfirmOrder';
import OpenCheckCust from './pages/OpenCheck';

export default function App() {
  return (
    <Router>
      <Switch>
          <Route
            exact
            path='/customer/table-ordering/:table_id'
            component={HomeCust}
          />
          {/* Sign in */}
          <Route exact path='/customer/signin' component={SignInCust} />
          {/* Sign up */}
          <Route exact path='/customer/signup' component={SignUpCust} />
          {/* Table ordering menu: */}
          <Route
            exact
            path='/customer/table-ordering/:table_id/menu'
            component={MenuCust}
          />
          {/* Item Detail */}
          <Route
            exact
            path='/customer/table-ordering/:table_id/menu/:item_id'
            component={ItemDetailCust}
          />
          {/* Update Order Item */}
          <Route
            exact
            path='/customer/table-ordering/:table_id/menu/:item_id/:order_item_id'
            component={ItemDetailCust}
          />
          {/* List of Brands Version 1 */}
          <Route
            exact
            path='/customer/table-ordering/:table_id/brands'
            component={BrandsCust}
          />
          {/* List of brands if users has  */}
          {/* Order Summary */}
          <Route
            exact
            path='/customer/checkout/:table_id'
            component={CheckoutSummmaryCust}
          />
          <Route
            exact
            path='/customer/confirm-order/:table_id'
            component={ConfirmOrderCust}
          />
          <Route
            exact
            path='/customer/open-check/:table_id'
            component={OpenCheckCust}
          />
          <Route
            exact
            path='/customer/pay-check/:table_id'
            component={PayCheckCust}
          />
          <Route
            exact
            path='/customer/pay-check-invoice/:table_id'
            component={PayCheckInvoiceCust}
          />
          <Route exact path='/customer/not-found' component={NotFoundCust} />
          {/* 404 */}
          <Route path="*" component={NotFoundCust}></Route>

      </Switch>
    </Router>
  );
}
